type Dir = string | null
type Mode = "dep" | "arr"

export function getAdlershof(
	mode: Mode,
	lineName: string,
	direction: Dir,
	provenance: Dir
) {
	if (mode === "arr" && provenance !== null) {
		switch (lineName) {
			case "M17":
				return ["S Adlershof [Tram Wendeschleife]", 6]
			case "61":
				if (provenance.includes("Schneweide"))
					return ["S Adlershof [Tram Rudower Chaussee]", 3]
				return ["S Adlershof [Tram Rudower Chaussee]", 2]
			case "63":
				if (provenance.includes("Johannisthal"))
					return ["S Adlershof [Tram Rudower Chaussee]", 3]
				return ["S Adlershof [Tram Rudower Chaussee]", 2]
			case "162":
				if (provenance.includes("Rudow"))
					return ["S Adlershof [Bus Rudower Chaussee]", 7]
				return ["S Adlershof [Bus Rudower Chaussee]", 1]
			case "163":
				if (provenance.includes("Schneweide"))
					return ["S Adlershof [Bus Rudower Chaussee]", 7]
				return ["S Adlershof [Bus Rudower Chaussee]", 1]
			case "164":
				if (provenance.includes("Kpenick"))
					return ["S Adlershof [Bus Rudower Chaussee]", 1]
				return ["S Adlershof [Bus Rudower Chaussee]", 7]
			case "260":
				return ["S Adlershof [Bus Rudower Chaussee]", 4]
			case "N60":
				if (provenance.includes("Flughafen"))
					return ["S Adlershof [Bus Rudower Chaussee]", 4]
				return ["S Adlershof [Bus Rudower Chaussee]", 1]
			case "N64":
			case "N68":
				return ["S Adlershof [Bus Rudower Chaussee]", 1]
			default:
				return ["S Adlershof", null]
		}
	} else if (mode === "dep" && direction !== null) {
		switch (lineName) {
			case "M17":
			case "27":
			case "67":
				return ["S Adlershof [Tram Wendeschleife]", 5]
			case "61":
				if (direction.includes("Schneweide"))
					return ["S Adlershof [Tram Rudower Chaussee]", 2]
				return ["S Adlershof [Tram Rudower Chaussee]", 3]
			case "63":
				if (direction.includes("Johannisthal"))
					return ["S Adlershof [Tram Rudower Chaussee]", 2]
				return ["S Adlershof [Tram Rudower Chaussee]", 3]
			case "162":
				if (direction.includes("Rudow"))
					return ["S Adlershof [Bus Rudower Chaussee]", 1]
				return ["S Adlershof [Bus Rudower Chaussee]", 7]
			case "163":
				if (direction.includes("Schneweide"))
					return ["S Adlershof [Bus Rudower Chaussee]", 1]
				return ["S Adlershof [Bus Rudower Chaussee]", 7]
			case "164":
				if (direction.includes("Kpenick"))
					return ["S Adlershof [Bus Rudower Chaussee]", 7]
				return ["S Adlershof [Bus Rudower Chaussee]", 1]
			case "260":
				return ["S Adlershof [Bus Rudower Chaussee]", 1]
			case "N60":
				if (direction.includes("Flughafen"))
					return ["S Adlershof [Bus Rudower Chaussee]", 1]
				return ["S Adlershof [Bus Rudower Chaussee]", 4]
			case "N64":
			case "N68":
				return ["S Adlershof [Bus Rudower Chaussee]", 4]
			default:
				return ["S Adlershof", null]
		}
	}
}

