export function getTreptowerPark(
	id: string,
	mode: Mode,
	lineName: string,
	direction: Dir,
	provenance: Dir
) {
	if (mode === "arr" && provenance !== null) {
		switch (id) {
			case "900000190100":
				switch (lineName) {
					case "M43":
						return ["Elsenstr./S Treptower Park [Bus Puschkinallee]", 2]
					case "165":
						if (provenance.includes("Mrkisches Museum"))
							return ["Elsenstr./S Treptower Park [Bus Elsenstr.]", 1]
						return ["Elsenstr./S Treptower Park [Bus Puschkinallee]", 2]
					case "166":
						if (provenance.includes("Weisestr"))
							return ["Elsenstr./S Treptower Park [Bus Puschkinallee]", 2]
						return ["Elsenstr./S Treptower Park [Bus Elsenstr.]", 1]
					case "194":
					case "N94":
						return ["Elsenstr./S Treptower Park [Bus Elsenstr.]", 1]
					case "265":
						if (provenance.includes("Schneweide"))
							return ["Elsenstr./S Treptower Park [Bus Puschkinallee]", 2]
						return ["Elsenstr./S Treptower Park [Bus Elsenstr.]", 1]
					case "N60":
					case "N65":
						if (provenance.includes("Alexanderplatz"))
							return ["Elsenstr./S Treptower Park [Bus Elsenstr.]", 1]
						return ["Elsenstr./S Treptower Park [Bus Puschkinallee]", 2]
					default:
						return ["Elsenstr./S Treptower Park", null]
				}
			case "900000190701":
				return ["S Treptower Park [Bus Puschkinallee]", 5]
			case "900000190702":
				switch (lineName) {
					case "M43":
						if (provenance.includes("Tunnelstr"))
							return ["S Treptower Park [Bus Treptowers]", 3]
						return ["S Treptower Park [Bus Treptowers]", 4]
					case "194":
						if (provenance.includes("Friedrichsfelde Ost"))
							return ["S Treptower Park [Bus Treptowers]", 3]
						if (provenance.includes("Weigel"))
							return ["S Treptower Park [Bus Treptowers]", 3]
						return ["S Treptower Park [Bus Treptowers]", 4]
					case "N94":
						if (provenance.includes("Magdalenenstr"))
							return ["S Treptower Park [Bus Treptowers]", 3]
						return ["S Treptower Park [Bus Treptowers]", 4]
					default:
						return ["S Treptower Park [Bus Treptowers]", null]
				}
		}
	} else if (mode === "dep" && direction !== null) {
		switch (id) {
			case "900000190100":
				switch (lineName) {
					case "M43":
						if (direction.includes("Fahrt endet hier"))
							return ["Elsenstr./S Treptower Park [Bus Puschkinallee]", 2]
						return ["Elsenstr./S Treptower Park [Bus Elsenstr.]", 1]
					case "165":
						if (direction.includes("Mrkisches Museum"))
							return ["Elsenstr./S Treptower Park [Bus Puschkinallee]", 2]
						return ["Elsenstr./S Treptower Park [Bus Elsenstr.]", 1]
					case "166":
						if (direction.includes("Boddinstr"))
							return ["Elsenstr./S Treptower Park [Bus Elsenstr.]", 3]
						return ["Elsenstr./S Treptower Park [Bus Puschkinallee]", 2]
					case "194":
					case "N94":
						return ["Elsenstr./S Treptower Park [Bus Elsenstr.]", 1]
					case "265":
						if (direction.includes("Schneweide"))
							return ["Elsenstr./S Treptower Park [Bus Elsenstr.]", 1]
						return ["Elsenstr./S Treptower Park [Bus Puschkinallee]", 2]
					case "N60":
					case "N65":
						if (direction.includes("Alexanderplatz"))
							return ["Elsenstr./S Treptower Park [Bus Puschkinallee]", 2]
						return ["Elsenstr./S Treptower Park [Bus Elsenstr.]", 1]
					default:
						return ["Elsenstr./S Treptower Park", null]
				}
			case "900000190701":
				return ["S Treptower Park [Bus Puschkinallee]", 5]
			case "900000190702":
				switch (lineName) {
					case "M43":
						if (direction.includes("Tunnelstr"))
							return ["S Treptower Park [Bus Treptowers]", 4]
						return ["S Treptower Park [Bus Treptowers]", 3]
					case "194":
						if (direction.includes("Friedrichsfelde Ost"))
							return ["S Treptower Park [Bus Treptowers]", 4]
						if (direction.includes("Weigel"))
							return ["S Treptower Park [Bus Treptowers]", 4]
						return ["S Treptower Park [Bus Treptowers]", 3]
					case "N94":
						if (direction.includes("Magdalenenstr"))
							return ["S Treptower Park [Bus Treptowers]", 4]
						return ["S Treptower Park [Bus Treptowers]", 3]
					default:
						return ["S Treptower Park [Bus Treptowers]", null]
				}
		}
	}
}

