export function getPankow(product: string) {
	switch (product) {
		case "suburban":
			return ["S Pankow", 1]
		case "subway":
			return ["U Pankow", 2]
		default:
			return ["S+U Pankow", 3]
	}
}

export function getSchnhauserAllee(product: string) {
	switch (product) {
		case "suburban":
			return ["S Schnhauser Allee", 1]
		case "subway":
			return ["U Schnhauser Allee", 2]
		default:
			return ["S+U Schnhauser Allee", 3]
	}
}
