type Dir = string | null
type Mode = "dep" | "arr"

export function getEichborndamm(
	mode: Mode,
	lineName: string,
	direction: Dir,
	provenance: Dir
) {
	if (mode === "arr" && provenance !== null) {
		switch (lineName) {
			case "221":
				if (provenance.includes("Leopoldplatz"))
					return "S Eichborndamm [Bus Eichborndamm]"
				if (provenance.includes("Schumacher"))
					return "S Eichborndamm [Bus Eichborndamm]"
				return "S Eichborndamm [Bus Antonienstr.]"
			case "322":
				if (provenance.includes("Paracelsus"))
					return "S Eichborndamm [Bus Eichborndamm]"
				return "S Eichborndamm [Bus Antonienstr.]"
			default:
				return "S Eichborndamm [Bus]"
		}
	} else if (mode === "dep" && direction !== null) {
		switch (lineName) {
			case "221":
				if (direction.includes("Leopoldplatz"))
					return "S Eichborndamm [Bus Antonienstr.]"
				if (direction.includes("Schumacher"))
					return "S Eichborndamm [Bus Antonienstr.]"
				return "S Eichborndamm [Bus Eichborndamm]"
			case "322":
				if (direction.includes("Paracelsus"))
					return "S Eichborndamm [Bus Antonienstr.]"
				return "S Eichborndamm [Bus Eichborndamm]"
			default:
				return "S Eichborndamm [Bus]"
		}
	}
}

export function getFranzNeumannPlatz(
	mode: Mode,
	lineName: string,
	direction: Dir,
	provenance: Dir
) {
	switch (lineName) {
		case "128":
			if (mode === "arr" && provenance?.includes("Osloer"))
				return [
					"U Franz-Neumann-Platz (Am Schfersee) [Bus Residenzstr. Sd]",
					6,
				]
			if (mode === "arr")
				return ["U Franz-Neumann-Platz (Am Schfersee) [Bus Hollnderstr.]", 2]
			if (mode === "dep" && direction?.includes("Osloer"))
				return ["U Franz-Neumann-Platz (Am Schfersee) [Bus Hollnderstr.]", 2]
			return ["U Franz-Neumann-Platz (Am Schfersee) [Bus Residenzstr. Sd]", 6]
		case "250":
			if (mode === "arr")
				return ["U Franz-Neumann-Platz (Am Schfersee) [Bus Markstr.]", 3]
			return ["U Franz-Neumann-Platz (Am Schfersee) [Bus Pankower Allee]", 4]
		case "327":
			if (mode === "arr" && provenance?.includes("Leopoldplatz"))
				return ["U Franz-Neumann-Platz (Am Schfersee) [Bus Markstr.]", 3]
			if (mode === "arr")
				return [
					"U Franz-Neumann-Platz (Am Schfersee) [Bus Residenzstr. Nord]",
					5,
				]
			if (mode === "dep" && direction?.includes("Leopoldplatz"))
				return [
					"U Franz-Neumann-Platz (Am Schfersee) [Bus Residenzstr. Nord]",
					5,
				]
			return ["U Franz-Neumann-Platz (Am Schfersee) [Bus Markstr.]", 3]
		case "U8":
		case "N8":
			if (mode === "arr" && provenance?.includes("Hermannstr"))
				return [
					"U Franz-Neumann-Platz (Am Schfersee) [Bus Residenzstr. Sd]",
					6,
				]
			if (mode === "arr")
				return [
					"U Franz-Neumann-Platz (Am Schfersee) [Bus Residenzstr. Nord]",
					5,
				]
			if (mode === "dep" && direction?.includes("Hermannstr"))
				return [
					"U Franz-Neumann-Platz (Am Schfersee) [Bus Residenzstr. Nord]",
					5,
				]
			return ["U Franz-Neumann-Platz (Am Schfersee) [Bus Residenzstr. Sd]", 6]
		default:
			return ["U Franz-Neumann-Platz (Am Schfersee) [Bus]", 7]
	}
}

export function getFrohnau(
	id: string,
	mode: Mode,
	lineName: string,
	direction: Dir,
	provenance: Dir
) {
	if (mode === "arr" && provenance !== null) {
		switch (id) {
			case "900000092201":
				switch (lineName) {
					case "125":
						if (provenance.includes("Invalidensiedlung"))
							return ["S Frohnau [Bus Ludolfingerpl.]", 2]
						return ["S Frohnau [Bus Ludolfingerpl.]", 1]
					case "220":
					case "N20":
						if (provenance.includes("Hainbuchenstr"))
							return ["S Frohnau [Bus Ludolfingerpl.]", 1]
						return ["S Frohnau [Bus Ludolfingerpl.]", 2]
					default:
						return ["S Frohnau [Bus Ludolfingerpl.]", null]
				}
			case "900000092282":
				switch (lineName) {
					case "125":
						return ["Zeltinger Platz/S Frohnau", 3]
					case "220":
					case "N20":
						if (provenance.includes("Hainbuchenstr"))
							return ["Zeltinger Platz/S Frohnau", 4]
						return ["Zeltinger Platz/S Frohnau", 3]
					case "806":
						return ["Zeltinger Platz/S Frohnau", 4]
					default:
						return ["Zeltinger Platz/S Frohnau", null]
				}
			default:
				return ["S Frohnau [Bus]", null]
		}
	} else if (mode === "dep" && direction !== null) {
		switch (id) {
			case "900000092201":
				switch (lineName) {
					case "125":
						if (direction.includes("Invalidensiedlung"))
							return ["S Frohnau [Bus Ludolfingerpl.]", 1]
						return ["S Frohnau [Bus Ludolfingerpl.]", 2]
					case "220":
					case "N20":
						if (direction.includes("Hainbuchenstr"))
							return ["S Frohnau [Bus Ludolfingerpl.]", 2]
						return ["S Frohnau [Bus Ludolfingerpl.]", 1]
					default:
						return ["S Frohnau [Bus Ludolfingerpl.]", null]
				}
			case "900000092282":
				switch (lineName) {
					case "125":
						return ["Zeltinger Platz/S Frohnau", 3]
					case "220":
					case "N20":
						if (direction.includes("Hainbuchenstr"))
							return ["Zeltinger Platz/S Frohnau", 3]
						return ["Zeltinger Platz/S Frohnau", 4]
					case "806":
						return ["Zeltinger Platz/S Frohnau", 4]
					default:
						return ["Zeltinger Platz/S Frohnau", null]
				}
			default:
				return ["S Frohnau [Bus]", null]
		}
	}
}

