export function getTegel(
	mode: Mode,
	lineName: string,
	direction: Dir,
	provenance: Dir
) {
	if (mode === "arr" && provenance !== null) {
		switch (lineName) {
			case "133":
				if (provenance.includes("Heiligensee")) return "S Tegel [Bus Buddestr.]"
				if (provenance.includes("Alt-Tegel")) return "S Tegel [Bus Buddestr.]"
				return "S Tegel [Bus Grudorfstr.]"
			case "N25":
				return "S Tegel [Bus Grudorfstr.]"
			default:
				return "S Tegel [Bus]"
		}
	} else if (mode === "dep" && direction !== null) {
		switch (lineName) {
			case "133":
				if (direction.includes("Heiligensee"))
					return "S Tegel [Bus Grudorfstr.]"
				if (direction.includes("Alt-Tegel")) return "S Tegel [Bus Grudorfstr.]"
				return "S Tegel [Bus Buddestr.]"
			case "N25":
				return "S Tegel [Bus Grudorfstr.]"
			default:
				return "S Tegel [Bus]"
		}
	}
}

