export function getHermannplatz(lineName: string) {
	switch (lineName) {
		case "U7":
			return ["U Hermannplatz ", 2]
		case "U8":
			return ["U Hermannplatz ", 1]
		default:
			return ["U Hermannplatz", 3]
	}
}

export function getHermannstr(product: string) {
	switch (product) {
		case "suburban":
			return ["S Hermannstr.", 1]
		case "subway":
			return ["U Hermannstr.", 2]
		default:
			return ["S+U Hermannstr.", 3]
	}
}

